import React from 'react'
import classes from './Content.module.css'
import InstagramEmbed from 'react-instagram-embed'; 
import PostContainer from '../Post/PostContainer'
import ImageUploladContainer from '../ImageUpload/ImageUploadContainer';


export default function Content({posts}) {
    return (
        <div className={classes.posts}>
            <div>
                <ImageUploladContainer/> 
                {
                    posts.map(({post, id}) => {
                        return <PostContainer key={id} postId={id} post={post}/>
                    })
                }
            </div>
            <InstagramEmbed
            clientAccessToken='483290653077860|b3ff09d76316c0ec418bfb4f616d2544'
            url='https://www.instagram.com/p/CLXotfTJIvj/'
            maxWidth={600}
            hideCaption={false}
            containerTagName='div'
            injectScript
            protocol=''
            onLoading={() => {}}
            onSuccess={() => {}}
            onAfterRender={() => {}}
            onFailure={() => {}}
            className={classes.inst}
            />
        </div>
        
    )
}
