import React, { useEffect, useState } from 'react'
import { db } from '../../Firebase'
import { useAuth } from '../../Context/AuthContext'
import firebase from 'firebase/app'
import Post from './Post'

function PostContainer({post, postId}) {

    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const {currentUser} = useAuth()
    const [disabled, setDisabled] = useState(true)


    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    useEffect(() => {
        let unsubscribe;
        if (postId) {
            unsubscribe = db 
            .collection('posts')
            .doc(postId)
            .collection('comments')
            /* .orderBy('timestamp', 'desc') */
            .onSnapshot((snapshot) => {
                setComments(snapshot.docs.map((doc) => doc.data()))
            })
        }
        return unsubscribe;
        
    }, [postId])
    console.log(comments)
    function handleSubmit(e) {
        e.preventDefault()
    }

    function postComment(e) {
        e.preventDefault()

        db.collection('posts').doc(postId).collection('comments').add({
            comment: comment,
            username: currentUser.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        setComment('')
    }

    function handleDelete() {
        db.collection('posts').doc(postId).delete()
    }


    function onLike() {
        db.collection('posts').doc(postId).update('likes', +post.likes + 1)
        
    }

    
    return (
            <Post handleShow={handleShow} post={post} postId={postId} show={show} handleClose={handleClose} 
            handleDelete={handleDelete} disabled={disabled} onLike={onLike} comments={comments} 
            comment={comment} handleSubmit={handleSubmit} postComment={postComment} setComment={setComment}/>
    )
}

export default PostContainer;
