import classes from './Login.module.css'
import React, { useRef, useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import logo from '../../../Common/img/logo.png'
import { useAuth } from '../../../Context/AuthContext'

export default function Login() {

    const emailRef = useRef()
    const passwordRef = useRef()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const {login} = useAuth()
    const history = useHistory()
    
    async function handleSubmit(e) {
        e.preventDefault()

        try {
            setError('')
            setLoading(true)
            await login(emailRef.current.value, passwordRef.current.value)
            history.push('/')
        } catch {
            setError('Failed to sign in')
        }
        setLoading(false)
    }


    return (
        <div className={classes.parent}>
            {error ? error : ''}
            <form className={classes.login} onSubmit={handleSubmit}>
                <h1 className={classes.title}><span className={classes.logo}></span></h1>
                <input autocomplete placeholder='Номер телефона, имя пользователя или элю.адрес' required ref={emailRef} className={classes.username}/>
                <input placeholder='Пароль' required ref={passwordRef} className={classes.password} type='password'/>
                <button disabled={login} className={classes.loginBtn}>Войти</button>
                <div className={classes.or}>ИЛИ</div>
                <NavLink to='/' className={classes.facebook}><i className='fa fa-facebook-square'/> Войти через Facebook</NavLink>
                <NavLink to='/forgot-password' className={classes.forgot}>Забыли пароль?</NavLink>
            </form>
            <div className={classes.signup}>
                <div>
                    У вас еще нет аккаунта?
                </div>
                <NavLink to='/sign-up' className={classes.signupLink}>Зарегистрироваться</NavLink>
            </div>
        </div>
    )
}
