import React, { useState } from 'react'
import { db, storage } from '../../Firebase'
import classes from './ImageUplolad.module.css'
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext'
import { Modal, Button } from 'react-bootstrap'
import logo from '../../Common/img/logo.png'


export default function ImageUplolad({handleShow, show, handleClose, progress, handleChange, caption, setCaption, handleUpload}) {
    return (
        <div className={classes.uploadPost}>
            <Button variant="secondary" className='w-100 ' onClick={handleShow}>
                Add post
            </Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title><img src={logo} alt='instagramClone'/></Modal.Title>
                </Modal.Header>
                    <Modal.Body>
                    <div className={classes.upload}>
                        <progress className={classes.progress} value={progress} max='100'></progress>
                        <textarea className={classes.input} placeholder='Описание:' value={caption} onChange={e => setCaption(e.target.value)}/>
                        <input className={classes.file} type='file' onChange={handleChange}/>
                    </div>
                    </Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
                <Button variant="primary" onClick={handleUpload}>
                    Upload
                </Button>
                </Modal.Footer>
            </Modal>
        </div>

    )
}
