import classes from './Messages.module.css'
import React from 'react'
import { NavLink } from 'react-bootstrap'

export default function Messages({currentUser, posts}) {
    console.log(posts)
    
    return (
        <div className={classes.messagesWrapper}>
            <div className={classes.dialogs}>
                <div className={classes.userName}>
                    {currentUser ? currentUser.email : ''}
                </div>
                <div>
                    {posts.map(post => {
                        return <NavLink key={post.id} className={classes.dialogsPerson}>
                            <img alt='ava' src={post.post.avatar} className={classes.ava}/>
                            <div>
                                {post.post.username}
                            </div> 
                        </NavLink>
                    })}
                </div>
            </div>

            <div className={classes.messages}>
                <div className={classes.messagesHeader}>
                    <div>
                        {posts[0] ? <img alt='ava' src={posts[0].post.avatar} className={classes.messagesAva}/>  : ''}
                    </div>
                    <div>
                        {posts[0] ? posts[0].post.username : ''}
                    </div>
                </div>
                <div className={classes.message}>
                    <div>Hi</div>
                    <form className={classes.messagesForm} /* onSubmit={handleSubmit} */>
                        <div className={classes.input}>
                            <input
                           /*  className={classes.input} */
                            placeholder='Напишите сообщение...'
                            /* value={comment}
                            onChange={(e) => setComment(e.target.value)} *//>
                            <button /* onClick={postComment} disabled={!comment}  */className={classes.btn}>Отправить</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
