import './App.css';
import AppHeader from './components/AppHeader/AppHeader';
import Content from './components/Content/Content';
import { AuthProvider } from './Context/AuthContext';
import {Route, Switch} from 'react-router-dom'
import Login from './components/Auth/Login/Login';
import SignUp from './components/Auth/SignUp/SignUp';
import PrivateRoute from './components/Hoc/PrivateRouter';
import ResetPassword from './components/Auth/ResetPassword/ResetPassword';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';
import AboutPost from './components/AboutPost/AboutPost';
import ContentContainer from './components/Content/ContentContainer';
import AppHeaderContainer from './components/AppHeader/AppHeaderContainer';
import AboutPostContainer from './components/AboutPost/AboutPostContainer';
import Messages from './components/Messages/Messages';
import MessagesContainer from './components/Messages/MessagesContainer';

function App() {

  return (
    <div className="App">
        <AuthProvider>
          <AppHeaderContainer/>
          <div className='container'>
                <Switch>
                  <PrivateRoute exact path='/' component={ContentContainer}/>
                  <Route  path='/login' component={Login}/>
                  <Route  path='/sign-up' component={SignUp}/>
                  <Route  path='/forgot-password' component={ResetPassword}/>
                  <Route  path='/p/:id' component={AboutPostContainer}/>
                  <Route  path='/messages' component={MessagesContainer}/>
                </Switch>
            </div>
        </AuthProvider>
      </div>
  );
}

export default App;
