const GET_LIKES = 'GET_LIKES'

const initialState = {
    show: false,
    likes: 0
}

export const postReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_LIKES:
            return {
                ...state,
                likes: action.payload
            }
        default:
            return state;
    }
}


export const setLikes = (likes) => {
    return {
        type: GET_LIKES,
        payload: likes
    }
}
