import React, { useState } from 'react'
import { db, storage } from '../../Firebase'
import classes from './ImageUplolad.module.css'
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext'
import { Modal, Button } from 'react-bootstrap'
import logo from '../../Common/img/logo.png'
import ImageUplolad from './ImageUplolad'


export default function ImageUploladContainer() {

    const [caption, setCaption] = useState('')
    const [progress, setProgress] = useState(0)
    const [image, setImg] = useState(null)
    const {currentUser} = useAuth()

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    function handleChange(e) {
        if (e.target.files[0]) {
            setImg(e.target.files[0])
        }
    };

    function handleUpload(e) {
        e.preventDefault()
        const uploadTask = storage.ref(`images/${image.name}`).put(image)

        uploadTask.on(
            'state_changed',
            (snapshot) => {
                const progress = Math.round(
                    (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                )
                setProgress(progress)
            },
            (error) => {
                console.log(error)
                alert(error.message)
            },
            () => {
                storage
                    .ref('images')
                    .child(image.name)
                    .getDownloadURL()
                    .then(url => {
                        db.collection('posts').add({
                            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                            caption: caption,
                            imageUrl: url,
                            username: currentUser.email,
                            avatar: url,
                            likes: 0,
                        })

                        setProgress(0)
                        setCaption('')
                        setImg(null)
                    })
            }
        )
    }

    return (
        <ImageUplolad handleShow={handleShow} show={show} handleClose={handleClose} progress={progress} 
                      handleChange={handleChange} caption={caption} setCaption={setCaption} handleUpload={handleUpload}/>
    )
}
