import classes from './ResetPassword.module.css'
import React, { useRef, useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import logos from '../../../Common/img/logos.png'
import { useAuth } from '../../../Context/AuthContext'

export default function ResetPassword() {

    const emailRef = useRef()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const [message, setMessage] = useState('')
    const {resetPassword} = useAuth()
    const history = useHistory()
    
    async function handleSubmit(e) {
        e.preventDefault()

        try {
            setError('')
            setLoading(true)
            await resetPassword(emailRef.current.value)
            history.push('/login')
            setMessage('Check your inbox')
        } catch {
            setError('Failed to sign in')
        }
        setLoading(false)
    }


    return (
        <div className={classes.parent}>
            <form className={classes.login} onSubmit={handleSubmit}>
                <h1 className={classes.title}><span className={classes.logo}></span></h1>
                <div className={classes.s}>Не удается войти?</div>
                <div className={classes.text}>Введите свой электронный адрес, имя пользователя или номер телефона, и мы отправим вам ссылку для восстановления доступа к аккаунту.</div>
                <input placeholder='Эл.адрес, телефон или имя пользователя' required ref={emailRef} className={classes.username}/>
                <button disabled={loading} className={classes.loginBtn}>Получить ссылку для изменения пароля</button>
                <div className={classes.or}>ИЛИ</div>
                <NavLink to='/sign-up' className={classes.facebook}> Создать новый аккаунт</NavLink>
            </form>
            <div className={classes.signup}>
                <NavLink to='/login' className={classes.signupLink}>Вернуться к входу</NavLink>
            </div>
        </div>
    )
}
