import React from 'react'
import './AppHeader.module.css'
import logo from '../../Common/img/logo.png'
import classes from './AppHeader.module.css'
import {NavLink} from 'react-router-dom'

export default function AppHeader({currentUser, handlelogout}) {
    return (
        <div className={classes.headerWrapper}>
            <div className={`${classes.header} container`}>
                <div className={classes.wrapper}>
                    <NavLink to={'/'}><img className={classes.logo} src={logo} alt='logo'/></NavLink> 
                </div>
                <div className={classes.wrapper}>
                    <input placeholder='Поиск' className={classes.input}/>
                </div>
                <div className={classes.wrapper}>
                    <div className={classes.icons}>
                        <NavLink className={classes.iconLink} to={'/'}>
                            <div className={classes.icon}><i className='fa fa-home'></i></div>
                        </NavLink>
                        <NavLink className={classes.iconLink} to={'/messages'}>
                        <div className={classes.icon}><i className='fa fa-comment-o'></i></div>
                        </NavLink>
                        
                        <div className={classes.icon}><i className='fa fa-compass'></i></div>
                        <div className={classes.icon}><i className='fa fa-heart-o'></i></div>
                        <div className={classes.icon}> 
                        {currentUser ? currentUser.email : ''} </div>
                        {currentUser ? <button onClick={handlelogout} className={classes.logout}>Log out</button> : ''}
                    </div>
                </div>
            </div>
        </div>
    )
}
