import classes from './SignUp.module.css'
import React, { useRef, useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import logo from '../../../Common/img/logo.png'
import { useAuth } from '../../../Context/AuthContext'


export default function SignUp() {

    const emailRef = useRef()
    const passwordRef = useRef()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const {signUp} = useAuth()
    const history = useHistory()
    
    async function handleSubmit(e) {
        e.preventDefault()

        try {
            setLoading(true)
            await signUp(emailRef.current.value, passwordRef.current.value)
            history.push('/')
        } catch {
            setError('Failed to create an account')
        }
        setLoading(false)
    }


    return (
        <div className={classes.parent}>
            <form className={classes.login} onSubmit={handleSubmit}>
                <h1 className={classes.title}><span className={classes.logo}></span></h1>
                <div className={classes.text}>
                Зарегистрируйтесь, чтобы смотреть фото и видео ваших друзей.
                </div>
                <button className={classes.facebookBtn}>
                <NavLink to='/' className={classes.facebook}> <i className='fa fa-facebook-square'/> Войти через Facebook</NavLink>
                </button>

                <div className={classes.or}>ИЛИ</div>
                <input placeholder='(Моб. телефон или эл.адрес)' className={classes.username}/>
                <input placeholder='(Имя и фамилия)' className={classes.password} type='text'/>
                <input placeholder='Email' required ref={emailRef} className={classes.password} type='email'/>
                <input placeholder='Пароль' required ref={passwordRef} className={classes.password} type='password'/>
                <button disabled={loading} className={classes.loginBtn}>Далее</button>
            </form>
            <div className={classes.signup}>
                <div>
                Есть аккаунт?
                </div>
                <NavLink to='/login' className={classes.signupLink}>Вход</NavLink>
            </div>
        </div>
    )
}
