import React, { useEffect, useState } from 'react'
import { db } from '../../Firebase';
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext';
import AboutPost from './AboutPost';

export default function AboutPostContainer(props) {

    const [info, setInfo] = useState('')
    const id = props.match.params.id;
    const [disabled, setDisabled] = useState(true)
    const [show, setShow] = useState(false);
    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const {currentUser} = useAuth()

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    useEffect(() => {
        let unsubscribe;
        if (id) {
            unsubscribe = db 
            .collection('posts')
            .doc(id)
            .onSnapshot((snapshot) => {
                setInfo(snapshot.data())
            })
        }
        return unsubscribe;
    }, [id])

    function handleDelete() {
        db.collection('posts').doc(id).delete()
    }

    function onLike() {
        db.collection('posts').doc(id).update('likes', +info.likes + 1)
    }

    function postComment(e) {
        e.preventDefault()

        db.collection('posts').doc(id).collection('comments').add({
            comment: comment,
            username: currentUser.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        setComment('')
    }

    function nameLength(name) {
        if (name.length >= 10) {
            name.slice(0, 5)
        }
    }

    return (
        <AboutPost info={info} show={show} handleClose={handleClose} handleShow={handleShow} disabled={disabled} 
        handleDelete={handleDelete} onLike={onLike} comment={comment} postComment={postComment} setComment={setComment} nameLength={nameLength}/>
    )
}
